import { Initializer } from '@adobe/elsie/lib';
import { Lang } from '@adobe/elsie/i18n';
import { ProductModel } from '@/pdp/data/models';

type ConfigProps = {
  langDefinitions?: Lang;
  defaultLocale?: string;
  models?: {
    [name: string]: {
      initialData: any;
      transform: (data?: ProductModel) => ProductModel;
      fallbackData?: (parentProduct: any, simpleProduct: ProductModel) => ProductModel;
    }
  }
};

export const initialize = new Initializer<ConfigProps>({
  init: async (config) => {
    const defaultConfig = { defaultLocale: 'en-US' };
    initialize.config.setConfig({ ...defaultConfig, ...config });
  },

  listeners: () => [],
});

export const config = initialize.config;
